<template>
  <v-container grid-list-md text-xs-center>
    <div id="app">
      <v-app id="inspire">
        <v-container grid-list-md text-xs-center>
          <v-layout>
            <!-- 连接 -->
            <v-flex xs12>
              <v-card>
                <v-card-text class="px-0">
                  <v-layout row wrap="" align-center>
                    <v-flex xs12>
                      <v-snackbar
                        v-model="snackbar"
                        :bottom="y === 'bottom'"
                        :color="alertcolor"
                        :left="x === 'left'"
                        :multi-line="mode === 'multi-line'"
                        :right="x === 'right'"
                        :timeout="timeout"
                        :top="y === 'top'"
                        :vertical="mode === 'vertical'"
                        >{{ text }}</v-snackbar
                      >
                      <v-toolbar flat color="white">
                        <v-toolbar-title>车辆台架连接操作</v-toolbar-title>
                      </v-toolbar>
                    </v-flex>
                    <v-flex xs1>
                      <v-subheader>车辆选择</v-subheader>
                    </v-flex>
                    <v-flex xs2>
                      <v-select
                        v-model="car_conn"
                        :items="car_items_conn"
                        item-text="carLicense"
                        item-value="abbr"
                        label="请选择要连接的车辆"
                        persistent-hint
                        return-object
                        single-line
                      ></v-select>
                      <!-- 操作车辆：{{car_conn.state}} -->
                    </v-flex>
                    <v-flex xs1></v-flex>
                    <v-flex xs1>
                      <v-subheader>台架选择</v-subheader>
                    </v-flex>
                    <v-flex xs2>
                      <v-select
                        v-model="platform_conn"
                        :items="platform_items_conn"
                        item-text="platformName"
                        item-value="abbr"
                        label="请选择要连接的台架"
                        persistent-hint
                        return-object
                        single-line
                      ></v-select>
                    </v-flex>
                    <v-flex xs2>
                      <v-btn id="add" color="success" @click="execute"
                        >连接</v-btn
                      >
                    </v-flex>
                  </v-layout>
                </v-card-text>
              </v-card>
            </v-flex>
          </v-layout>
          <v-layout row wrap="">
            <!-- 下面的数据 -->
            <v-flex xs12>
              <v-card color="rgba(238,238,238)">
                <v-card-text class="px-0">
                  <!-- 具体的值 -->
                  <template>
                    <div>
                      <!-- flat的意思是去掉边框阴影 -->
                      <v-toolbar flat color="white">
                        <v-toolbar-title>车辆台架状态展示</v-toolbar-title>
                      </v-toolbar>
                      <v-data-table
                        :headers="headers"
                        :items="car_plat_list"
                        rows-per-page-text="每页条数"
                        :rows-per-page-items="rowsPerPageItems"
                      >
                        <template v-slot:items="props">
                          <td class="text-xs-center">
                            {{ props.item.carInfo.carLicense }}
                          </td>
                          <td class="text-xs-center">
                            {{ props.item.platformInfo.platformName }}
                          </td>
                          <td class="text-xs-center">
                            {{ props.item.create_at }}
                          </td>
                          <td class="text-xs-center">
                            <v-btn
                              id="add"
                              color="success"
                              @click="
                                dis_conn(
                                  props.item,
                                  props.item.carInfo.carId,
                                  props.item.platformInfo.platformId
                                )
                              "
                              >断开</v-btn
                            >
                          </td>
                        </template>
                        <template v-slot:no-data>
                          <p>暂无已经连接的车辆</p>
                        </template>
                      </v-data-table>
                    </div>
                  </template>
                </v-card-text>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-app>
    </div>
    <v-layout></v-layout>
  </v-container>
</template>
<script>
import { mapState, mapGetters } from 'vuex'
import { platform } from 'os'
export default {
  data() {
    return {
      // 连接状态
      headers: [
        { text: '车辆', align: 'center', value: 'car' },
        { text: '架台', align: 'center', value: 'platform' },
        { text: '连接时间', align: 'center', value: 'create_at' },
        { text: '断开', align: 'center' }
      ],
      rowsPerPageItems: [5, 10],
      // 弹出框
      snackbar: false,
      y: 'top',
      alertcolor: '',
      x: null,
      mode: '',
      timeout: 3000,
      text: '',
      // 弹出框结束
      plat_msg: '',
      car_msg: '',
      plat_chosen: [],

      // 架台选项
      platform_conn: {},
      platform_items_conn: [],
      // 车辆选项
      car_conn: {},
      // 下拉框列表
      car_items_conn: [],

      car_platform: [],
      // 状态列表展示
      car_plat_list: [
        {
          carInfo: '',
          platformInfo: '',
          create_at: ''
        }
      ]
    }
  },
  created: function() {
    this.init_plat()
    this.init_car()
    this.state_data()
  },
  sockets: {
    connect: function() {
      this.id = this.$socket.id
    },
    server_response: function(res) {
      let deviceid = res.data.deviceid
      if (res.data.type == '1') {
        // 传的是设备名称以及需要向请求的路径
        this.search_msg(deviceid, 'getCarinfo/')
      } else if (res.data.type == '2') {
        this.search_msg(deviceid, 'getPlatforminfo/')
      }
    }
  },
  methods: {
    // 初始化架台
    init_plat() {
      this.$axios({
        method: 'get',
        url: 'getPlatformlist/'
      }).then((res) => {
        let data = res.data.data
        this.handle_data(data, 'platform')
      })
    },
    // 初始化车辆
    init_car() {
      this.$axios({
        method: 'get',
        url: 'getCarlist/'
      }).then((res) => {
        let data = res.data.data
        this.handle_data(data, 'car')
      })
    },
    // 初始化状态
    state_data() {
      this.$axios({
        method: 'get',
        url: 'connectGetlist/'
      })
        .then((res) => {
          this.car_plat_list = res.data.data
        })
        .catch((res) => {
          let error = res.message
          if (error.indexOf('Network') !== -1) {
            this.snackbar = true
            this.alertcolor = 'error'
            this.text = '网络异常'
          }
        })
    },
    // 将axios初始化请求回来的数据进行筛选，把已经在线的且没有连接的分别放入对应的车和台架列表中
    handle_data(data, deviced) {
      // 定义一个临时数据，取出在线的设备,下面操作的全是在线的设备
      let temparr = []
      for (var i = 0; i < data.length; i++) {
        // 在线且未连接的放进列表
        if (data[i].online == 1 && data[i].is_conn == 0) {
          temparr.push(data[i])
        }
      }
      // 将在线设备赋值给data
      data = temparr
      // console.log(data)
      if (deviced == 'car') {
        //  车辆的处理====>将原来数据清空，然后把新的数据push到原来数组上
        this.car_items_conn = data
      } else {
        //  台架的处理
        this.platform_items_conn = data
      }
    },
    // 根据socketio传来的数据请求真实数据
    search_msg(deviceid, getinfo) {
      // 如果是车,请求车
      if (getinfo == 'getCarinfo/') {
        this.$axios({
          method: 'get',
          url: getinfo,
          params: {
            carId: deviceid
          }
        }).then((res) => {
          // 状态展示改变
          this.show_state(res, 'car')
          // 车辆下拉框列表改变
          this.change_msg(res.data, deviceid, '1')
        })
      } else {
        // 如果是台架
        this.$axios({
          method: 'get',
          url: getinfo,
          params: {
            platformId: deviceid
          }
        }).then((res) => {
          // 状态展示改变
          this.show_state(res, 'platform')
          // 台架下拉框列表改变
          this.change_msg(res.data, deviceid, '2')
        })
      }
    },
    // 根据返回的信息进行改变
    change_msg(res, deviceid, devicednum) {
      // 判断是车还是台架
      if (devicednum == 1) {
        // 找到这辆车的在线状态
        let present_arr = []
        for (var i = 0; i < this.car_items_conn.length; i++) {
          present_arr.push(this.car_items_conn[i].carId)
        }
        // 如果在现有数组中存在，那么就是在线
        if (present_arr.indexOf(deviceid) > -1) {
          if (res.data.online == 0) {
            console.log('让车辆' + deviceid + '下线')
            // 遍历删除那一项
            for (var j = 0; j < this.car_items_conn.length; j++) {
              if (this.car_items_conn[j].carId == res.data.carId) {
                this.car_items_conn.splice(j, 1)
              }
            }
          } else {
            console.log('更改' + deviceid + '属性')
          }
        } else {
          if (res.data.online == 1) {
            var lengths = this.car_items_conn.length
            this.car_items_conn.splice(lengths, 1, res.data)
          } else {
            console.log('已经下线，无法再次下线')
          }
        }
      } else if (devicednum == 2) {
        let present_arr = []
        for (var i = 0; i < this.platform_items_conn.length; i++) {
          present_arr.push(this.platform_items_conn[i].platformId)
        }
        // 如果在现有数组中存在，那么就是在线
        if (present_arr.indexOf(deviceid) > -1) {
          if (res.data.online == 0) {
            console.log('让台架' + deviceid + '下线')
            // 遍历删除那一项
            for (var j = 0; j < this.platform_items_conn.length; j++) {
              if (
                this.platform_items_conn[j].platformId == res.data.platformId
              ) {
                console.log(this.platform_items_conn)
                this.platform_items_conn.splice(j, 1)
                console.log(this.platform_items_conn)
              }
            }
          } else {
            console.log('更改' + deviceid + '属性')
          }
        } else {
          if (res.data.online == 1) {
            console.log(deviceid + '让该台架上线')
            let lengths = this.platform_items_conn.length
            this.platform_items_conn.splice(lengths, 1, res.data)
          } else {
            console.log('已经下线，无法再次下线')
          }
        }
      }
    },
    // 点击连接事件
    execute() {
      this.$axios({
        method: 'post',
        url: 'connectstatusdevice/',
        data: {
          carId: this.car_conn.carId,
          platformId: this.platform_conn.platformId,
          is_conn: 1
        },
        transformRequest: [
          function(data) {
            let ret = ''
            for (let it in data) {
              ret +=
                encodeURIComponent(it) +
                '=' +
                encodeURIComponent(data[it]) +
                '&'
            }
            return ret
          }
        ],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((res) => {
        // 当成功时候，
        if (res.data.code == 200) {
          //提示信息，
          this.snackbar = true
          this.alertcolor = 'green'
          this.text = res.data.msg
          //展示列表重新渲染
          this.state_data()
          //点击连接按钮响应成功时删除对应的条数
          this.delete_msg(this.car_conn.carId, this.platform_conn.platformId)
        } else {
          this.snackbar = true
          this.alertcolor = 'error'
          this.text = res.data.msg
        }
      })
    },
    // 删除操作
    delete_msg(carId_cur, platformId_cur) {
      for (var j = 0; j < this.platform_items_conn.length; j++) {
        if (this.platform_items_conn[j].platformId == platformId_cur) {
          console.log(this.platform_items_conn)
          this.platform_items_conn.splice(j, 1)
          console.log(this.platform_items_conn)
        }
      }
      for (var j = 0; j < this.car_items_conn.length; j++) {
        if (this.car_items_conn[j].carId == carId_cur) {
          console.log(this.car_items_conn)
          this.car_items_conn.splice(j, 1)
          console.log(this.car_items_conn)
        }
      }
    },
    // 点击断开操作
    dis_conn(props_item, carId, platformId) {
      // console.log(carId)
      // console.log(platformId)
      this.$axios({
        method: 'post',
        url: 'connectstatusdevice/',
        data: {
          platformId: platformId,
          carId: carId,
          is_conn: 0
        },
        transformRequest: [
          function(data) {
            let ret = ''
            for (let it in data) {
              ret +=
                encodeURIComponent(it) +
                '=' +
                encodeURIComponent(data[it]) +
                '&'
            }
            return ret
          }
        ],
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((res) => {
        // console.log(res)
        if (res.data.code == 200) {
          this.snackbar = true
          this.alertcolor = 'success'
          this.text = res.data.msg
          const index = this.car_plat_list.indexOf(props_item)
          this.car_plat_list.splice(index, 1)
          this.init_plat()
          this.init_car()
        }
      })
    },
    // socketio处理下面展示列表的数据
    show_state(res, deviced) {
      console.log(res)
      let car_arr = []
      let platform_arr = []
      // 如果是车辆，将所有的车辆id放进car_arr数组中，
      // 如果是台架，将所有的台架id放入platform_arr数组中
      for (var i = 0; i < this.car_plat_list.length; i++) {
        console.log(this.car_plat_list[i])
        if (deviced == 'car') {
          // 将所有的carId推进新数组
          car_arr.push(this.car_plat_list[i].carInfo.carId)
          console.log(this.car_plat_list[i].carInfo.carId)
        } else if (deviced == 'platform') {
          // console.log(this.car_plat_list[i].platformInfo.platformId)
          //将所有的platformId推进新数组
          platform_arr.push(this.car_plat_list[i].platformInfo.platformId)
        }
      }
      // console.log(car_arr)
      // console.log(platform_arr)
      // 如果车辆掉线
      if (deviced == 'car') {
        // 如果在数组中,删除car这一组，如果不在数组中说明没有连接，不进行操作
        if (car_arr.indexOf(res.data.data.carId) > -1) {
          // 判断掉线的一项的index, 删除
          for (var i = 0; i < car_arr.length; i++) {
            if (car_arr[i] == res.data.data.carId) {
              // 如果请求回来的设备不在线的，删除整条数据
              if (res.data.data.is_conn == 0) {
                console.log('车辆删除')
                this.car_plat_list.splice(i, 1)
                // 如果还在线的情况下，修改了属性，直接去替换
              } else {
                console.log('车辆修改')
                this.car_plat_list[i].carInfo = res.data.data
              }
            }
          }
        }
        // 如果台架掉线
      } else if (deviced == 'platform') {
        if (platform_arr.indexOf(res.data.data.platformId) > -1) {
          // 判断掉线的一项的index, 删除
          for (var i = 0; i < platform_arr.length; i++) {
            if (platform_arr[i] == res.data.data.platformId) {
              // 如果请求回来的设备不在线的，删除整条数据
              if (res.data.data.is_conn == 0) {
                console.log('台架删除')
                this.car_plat_list.splice(i, 1)
                // 如果还在线的情况下，修改了属性，直接去替换
              } else {
                console.log('台架修改')
                this.car_plat_list[i].platformInfo = res.data.data
              }
            }
          }
        }
      }
      this.init_plat()
      this.init_car()
    }
  }
}
</script>
